import React, { useEffect, useState } from "react";
import axios from "axios";
import DiningLayout from '../layouts/DiningLayout';
import "../css/Find_store.css";

const Find_store = () => {
  const [restaurants, setRestaurants] = useState([]);
  const [error, setError] = useState('');

  useEffect(() => {
    axios.get('http://localhost:18080/api/restaurants')
        .then((response) => {
          setRestaurants(response.data);
        })
        .catch((error) => {
          setError(error.message);
          console.error("Error fetching restaurants:", error);
        });
  }, []);

  return (
      <DiningLayout>
        <div className="find-store-container">
          <h1>식당 리스트</h1>
          {error && <p className="error-message">오류: {error}</p>}
          <div className="restaurant-list">
            {restaurants.map((restaurant, index) => (
                <div key={index} className="restaurant-card">
                  <h2>{restaurant.name}</h2>
                  <p>{restaurant.address}</p>
                  <p>카테고리: {restaurant.category}</p>
                  <p>지번: {restaurant.oldAddress}</p>
                  <p>전화번호: {restaurant.phoneNumber}</p>
                  <p>홈페이지: {restaurant.homepage}</p>
                  <p>영업시간: {restaurant.openTime}</p>
                  <p>휴무일: {restaurant.offDays}</p>
                  <p>태그: {restaurant.tags}</p>
                </div>
            ))}
          </div>
        </div>
      </DiningLayout>
  );
};

export default Find_store;